@import "../../../styles/vars";
@import "../../../styles/mixins";


.indBlogDescription {
  background: $color-black;
  padding-top: 302px;
  @include setDistance(padding-bottom,140);

  @include xxlScreenMedia {
    padding-top: 297px;
  }

  @include xlScreenMedia {
    padding-top: 292px;
  }

  @include lgScreenMedia {
    padding-top: 282px;
  }

  @include mdScreenMedia {
    padding-top: 200px;
  }

  @include smScreenMedia {
    padding-top: 162px;
  }

  @media (max-width: 400px) {
    padding-top: 110px;
  }

  &__userBlock {
    @include flexCol(flex-start);
    margin-bottom: 32px;

    @media (min-width: $md-screen) {
      display: none;
    }
  }

  &__text {
    &:not(:last-child) {
      margin-bottom: 26px;

      @include smScreenMedia {
        margin-bottom: 20px;
      }
    }

    img {
      display: block;
      margin: 10px auto;
      max-width: 100%;
      width: 100%;
    }

    ul, ol {
      margin-top: 10px;
      margin-left: 35px;
    }

    li {
      margin-bottom: 8px;
    }

    a {
      text-decoration: none;
      color: $color-light-blue;
        &:hover, &:active {
        text-decoration: underline;
      }
    }

    h1 {
      margin-bottom: 15px;
      margin-top: 15px;

      @include setFont(42px, 500);
      color: $color-white;

      @include xxlScreenMedia {
        font-size: 36px;
      }

      @include xlScreenMedia {
        font-size: 30px;
      }

      @include lgScreenMedia {
        font-size: 25px;
      }

      @include smScreenMedia {
        font-size: 22px;
      }
    }
  }

  h2 {
    margin-bottom: 10px;
    margin-top: 10px;
    
    @include setFont(36px, 500);
    color: $color-white;
}

  p {
    margin-bottom: 21px;
    @include smScreenMedia {
      margin-bottom: 11px;
    }
  }
  strong {
    font-weight: 500;
  }
}